  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

@media (min-width: 768px) {
  body {
    background-color: hsl(234, 29%, 20%);
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 100rem;
    height: 64.3rem;
    border-radius: 3rem;
    background-color: white;
    display: flex;
    flex-direction: row-reverse;
    margin: 7.2rem auto;
  }
}
.container header .img-desktop {
  display: none;
}
@media (min-width: 768px) {
  .container header .img-mobile {
    display: none;
  }
  .container header .img-desktop {
    display: block !important;
    width: 40rem;
    height: 58.5rem;
    margin: 2.3rem;
  }
}
.container .information {
  margin: 0 2.5rem;
  color: hsl(234, 29%, 20%);
}
.container .information h1 {
  font-weight: 700;
  font-size: 4rem;
  text-align: start;
}
@media (min-width: 768px) {
  .container .information h1 {
    font-size: 6rem;
  }
}
.container .information p {
  padding-right: 2rem;
}
.container .information ul {
  margin: 0;
  padding: 0;
}
.container .information ul li {
  list-style: none;
  display: flex;
}
.container .information ul li span {
  align-items: center;
  justify-content: center;
  margin-bottom: 1.3rem;
}
.container .check-cont #check {
  margin-right: 1.3rem;
}
.container .mail {
  display: flex;
  flex-direction: column;
  margin: 2rem 2.5rem;
}
.container .mail span {
  font-weight: 700;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}
.container .mail input {
  border-radius: 1rem;
  border: 0.1rem solid hsl(231, 7%, 60%);
  margin-bottom: 3rem;
  text-indent: 2.5rem;
}
.container .mail .alerta {
  border: 0.1rem solid hsl(4, 100%, 67%) !important;
  background-color: #ffe7e6;
  color: hsl(4, 100%, 67%);
}
.container .mail .alerta::placeholder {
  color: hsl(4, 100%, 67%) !important;
}
.container .mail .alerta:focus {
  border-color: hsl(4, 100%, 67%) !important;
  outline: none;
}
.container .mail button {
  background-color: hsl(234, 29%, 20%);
  color: white;
  border: none;
  border-radius: 1rem;
  font-weight: 700;
  margin-bottom: 4.1rem;
  transition: all 0.3s ease-in-out;
}
.container .mail button:hover {
  cursor: pointer;
  background-color: hsl(4, 100%, 67%);
  box-shadow: 0 0 10px hsl(4, 100%, 67%);
}
.container .mail .information-alert {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .container main {
    width: 50.3rem;
    height: 44.3rem;
    margin-top: 5rem;
  }
}

input,
button {
  width: 32.7rem;
  height: 5.5rem;
}
@media (min-width: 768px) {
  input,
  button {
    width: 100%;
  }
}

button {
  background-color: hsl(234, 29%, 20%);
  color: white;
  border: none;
  border-radius: 1rem;
  font-weight: 700;
  margin-bottom: 4.1rem;
  transition: all 0.3s ease-in-out;
}
button:hover {
  cursor: pointer;
  background-color: hsl(4, 100%, 67%);
  box-shadow: 0 0 10px hsl(4, 100%, 67%);
}

.error {
  color: hsl(4, 100%, 67%);
  display: block;
}

.mensaje {
  display: none;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
@media (min-width: 768px) {
  .attribution {
    color: white;
    margin-top: 4rem;
  }
}
.attribution a {
  color: hsl(228, 45%, 44%);
}
@media (min-width: 768px) {
  .attribution a {
    color: hsl(4, 100%, 67%) !important;
  }
}

.message {
  color: hsl(234, 29%, 20%);
  margin-top: 14.8rem;
  margin-left: 2.5rem;
  margin-right: 2.5rem;
  line-height: 1.2;
}
.message p,
.message h1,
.message h2,
.message h3,
.message span {
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .message {
    width: 50.3rem;
    height: 52rem;
    background-color: white;
    border-radius: 3rem;
    margin: 0 auto;
    margin-top: 14.8rem;
  }
}
@media (min-width: 768px) {
  .message section {
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-right: 6.9rem;
    padding-left: 6.9rem;
  }
}
.message p {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .message p {
    font-size: 1.6rem;
    margin-top: 4.2rem;
    margin-bottom: 4.5rem;
  }
}
.message p span {
  font-weight: 700;
}

.message__header img {
  width: 6.6rem;
  height: 6.9rem;
}
@media (min-width: 768px) {
  .message__header img {
    margin-top: 5rem;
  }
}
.message__header h1 {
  font-size: 3.8rem;
  font-weight: 700;
  text-align: start;
  margin-top: 2.5rem;
}

.button {
  margin-top: 27.1rem;
}
@media (min-width: 768px) {
  .button {
    margin-top: 0;
  }
}